import React from 'react';

import { Bold, hasData } from './shared';
import ResearchProjects from './ResearchProjects';
import meiSrc from "../img/mei.jpg";

import "antd/dist/antd.css";

const newsData = require('../data/news.json');

const Body = () => (
    <div>
        <p>
            Hey there—nice to meet you! I'm Jenny. I'm a first year Ph.D. student at <a href="https://www.cmu.edu/" target="_blank" rel="noreferrer">
            Carnegie Mellon University</a> advised by <a href="http://www.cs.cmu.edu/~bam/" target="_blank" rel="noreferrer">Dr. Brad A. Myers</a>.
        </p>
        <p>
            <Bold>I research ways to make programming better for everyone</Bold> by studying developers, programming communities, and building 
            intelligent tools that make building software easier. My work has spanned across machine learning, human-computer interaction, 
            software engineering, and networks, but nowadays my main focus is on the first three topics.
        </p>
        <p>
            Have any questions or just want to chat? I'm always happy to meet new people—just reach out! And don't forget to say hi to my dog, {' '}
            <a href={meiSrc} target="_blank" rel="noreferrer">Mei 🐕</a>!
        </p>
        {/* News section */}
        {hasData(newsData) && (
            <React.Fragment>
                <h3>News</h3>
                <ul>
                    {newsData
                        .map((e: {date: string, description: string}, i: number ) => {
                            return <li key={i}> <Bold>[{e.date}]</Bold> {e.description}</li>     
                        })
                    }
                </ul>
            </React.Fragment>
        )}

        <ResearchProjects/>
    </div>
);

export default Body;